import { ObtenerFoto } from "./ObtenerFoto"

export function Ingreso(props) {
    const {
        nombre,
        precio,
        stock,
        marca,
        categoria,
        detalles,
        foto,
        envio,
    } = props.producto
    const { onChange, onSubmit, valido, editarID, escribirCampoUrlFoto } = props

    return (
        <form className="alta-form" onSubmit={onSubmit}>
            {/* <!-- campo de ingreso de nombre de producto --> */}
            <div className="mi-input-group">
                <label htmlFor="nombre">nombre</label>
                <input className="entrada" id="nombre" type="text" name="nombre" value={nombre} onChange={onChange} />
                <div className="error-details"></div>
            </div>

            {/* <!-- campo de ingreso de precio de producto --> */}
            <div className="mi-input-group">
                <label htmlFor="precio">precio</label>
                <input className="entrada" id="precio" type="number" step="0.00" name="precio" value={precio} onChange={onChange} />
                <div className="error-details"></div>
            </div>

            {/* <!-- campo de ingreso de stock de producto --> */}
            <div className="mi-input-group">
                <label htmlFor="stock">stock</label>
                <input className="entrada" id="stock" type="number" name="stock" value={stock} onChange={onChange} />
                <div className="error-details"></div>
            </div>

            {/* <!-- campo de ingreso de marca de producto --> */}
            <div className="mi-input-group">
                <label htmlFor="marca">marca</label>
                <input className="entrada" id="marca" type="text" name="marca" value={marca} onChange={onChange} />
                <div className="error-details"></div>
            </div>

            {/* <!-- campo de ingreso de categoria de producto --> */}
            <div className="mi-input-group">
                <label htmlFor="categoria">categora</label>
                <input className="entrada" id="categoria" type="text" name="categoria" value={categoria} onChange={onChange} />
                <div className="error-details"></div>
            </div>

            {/* <!-- campo de ingreso de detalles de producto --> */}
            <div className="mi-input-group">
                <label htmlFor="detalles">detalles</label>
                <input className="entrada" id="detalles" type="text" name="detalles" value={detalles} onChange={onChange} />
                <div className="error-details"></div>
            </div>

            {/* <!-- campo de ingreso de la foto del producto --> */}
            <div className="mi-input-group">
                <label htmlFor="foto">foto</label>
                <input className="entrada" id="foto" type="text" name="foto" value={foto} onChange={onChange} />
                <div className="error-details"></div>
            </div>

            {/* Zona de obtencin de la foto del producto */}            
            <ObtenerFoto escribirCampoUrlFoto={escribirCampoUrlFoto} />

            {/* <!-- campo de condicin envo --> */}
            <div className="mi-input-group">
                <input className="entrada mr-1" id="envio" type="checkbox" name="envio" checked={envio} onChange={onChange} /> 
                <label htmlFor="envio">envo</label>
            </div>

            <button disabled={!valido} className={ editarID? "btnActualizar":"btnAgregar"}>
                { editarID? 'Actualizar': 'Agregar' }
            </button>
        </form>
    )
}