import { useEffect, useRef, useState } from 'react'
import './Chat.css'
import { useSelector } from 'react-redux'

import * as servicioMensajesWebsocket from '../servicios/mensajes'

export const Chat = () => {
    //const [nombre, setNombre] = useState('')
    const [texto, setTexto] = useState('')

    const [mensajes, setMensajes] = useState([])

    const usuario = useSelector(state => state.usuarioLogueado)
    const { nombre, admin } = usuario

    const refTexto = useRef()

    useEffect(() => {
        servicioMensajesWebsocket.conectar()
        servicioMensajesWebsocket.suscribirAlCanal('mensajes', mensajes => {
            console.log('mensajes:', mensajes)
            setMensajes(mensajes)
        })

        return () => {
            servicioMensajesWebsocket.desconectar()
        }
    },[])

    const onSubmit = e => {
        e.preventDefault()

        //console.log('nombre:', nombre)
        //console.log('texto:', texto)
        const mensaje = { autor: nombre, texto, admin, fyh: new Date().toLocaleString() }
        servicioMensajesWebsocket.enviarMensaje('nuevo-mensaje', mensaje)

        /* const mensajesClon = [...mensajes]
        mensajesClon.push(mensaje)
        setMensajes(mensajesClon) */

        setMensajes([...mensajes, mensaje])

        refTexto.current.focus()
        setTexto('')
    }

    return (
        <div className="Chat mt-5">
            <div className="jumbotron w-75 m-auto">
                <h3>Chat</h3>
                <hr />

                <div>
                    {
                        mensajes.filter(mensaje => mensaje.autor === nombre || mensaje.admin || admin).map( (mensaje, index) => 
                            <div key={index}>
                                <b>[{mensaje.fyh}] { mensaje.autor } - </b>
                                <span>{ mensaje.admin? '(Admin)': (admin? '(User)':'(Yo)') }: </span>
                                <i>{ mensaje.texto }</i>
                            </div>
                        )
                    }
                </div>

                <form className='form-inline mt-1' onSubmit={onSubmit}>
                    <input type="text" className='form-control w-25 mr-2 text-muted' placeholder='nombre' value={nombre} readOnly/>
                    <input type="text" className='form-control w-25 mr-3' placeholder='ingrese mensaje' value={texto} ref={refTexto} onChange={
                        e => setTexto(e.target.value)
                    } />

                    <input type="submit" className='btn btn-success' value="Enviar" />
                </form>
            </div>
        </div>

    )
}